* {
  font-family: Helvetica, Arial, sans-serif;
}

body {
  padding: 60px;
}

input {
  color: grey;
  font-size: 1.2rem;
  width: 100%;
}

.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right: 10px;
}

.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  max-height: 170px;
  width: 100%;
}

.card-grid{
  display: grid;
  /* flex-direction: row;
  flex-wrap: wrap; */
  gap: 1rem;
  grid-template-columns: repeat(auto-fill ,minmax(12rem, 1fr));
}